body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:"Josefin Sans",sans-serif}main{margin:0 5% 0 5%}

*{font-family:"Josefin Sans",sans-serif}main{margin:0 5% 0 5%}.App-header{background-color:#b040be;height:12vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:20px;color:white}

*{font-family:"Josefin Sans",sans-serif}main{margin:0 5% 0 5%}.App-footer{background-color:#a19e99;height:8vh;width:100%;bottom:0;color:#b040be;display:flex;align-items:center;justify-content:center;position:relative}.App-footer p{text-align:left;width:100%;padding-left:50px;font-weight:700;font-size:12px}

*{font-family:"Josefin Sans",sans-serif}main{margin:0 5% 0 5%}.form{margin:70px 0;display:flex;flex-direction:row;width:100%;height:40px;justify-content:space-around;align-items:center}.form fieldset{width:42%;height:100%;padding:20px}.form input{height:30px;width:60%}.form button{margin-left:40px;height:90%;align-self:center;width:100px;background:#d9e4dd;color:#b040be;border:none}.Show{height:100px;width:95%;display:flex;margin:auto;justify-content:center;align-items:center;border:1px solid black}.Show span{margin:5px}

